const axios = require('axios');
const { dbPath } = require('../constants');

export const getSingleTransactions = async (month, callback) => {

	axios.get(`${dbPath}/singleTransactions?month=${month}`, {
		method: 'GET',
		mode: 'no-cors',
		redirect: 'follow'
	})
		.then(function (response) {
			callback(response?.data);
		})
		.catch(function (error) {
			console.log(error);
		});
};
