import React from 'react';
import { AllDataSection } from './AllDataSection';
import { StyledSection } from './StyledSection';
import { SingleTransaction } from './SingleTransaction';
import { getMonthName } from './utils/getMonth';


export const AllData = ({ data, month }) => {
	const { variables, fixed, inc, allTransactionOfMonth } = data;
	return (
		<StyledSection>
			<AllDataSection data={variables} title="Variabili" month={month}/>
			<AllDataSection data={fixed} title="Fisse" month={month}/>
			<AllDataSection data={inc} title="Entrate" month={month}/>
			<h3>Spese singole di {getMonthName()}</h3>
			{allTransactionOfMonth.reverse().map(singleTransaction =>
				<SingleTransaction
					name={variables.find(variable => variable.id === singleTransaction.id)?.name}
					amount={singleTransaction.amount}
					day={singleTransaction.day}
				/>
			)}
		</StyledSection>
	);
};

