import React from 'react';
import styled from 'styled-components/macro';

const StyledSingleTransaction = styled.div`
	display: flex;
	border-bottom: solid 1px #ccc;
  justify-content: space-between;
  align-items: center;
`

export const SingleTransaction = ({ name, amount, day }) => {
	return name ? (
		<StyledSingleTransaction>
			<div>{day.split('-')[2]} - {name}</div>
			<div>{amount}€</div>
		</StyledSingleTransaction>
	) : null
}

