import "./App.scss";
import { ThemeProvider } from 'styled-components';
import { createContext, useEffect, useState } from "react";
import { Categories } from "./Categories";
import { getWeek } from "./utils/getWeek";
import { AddAmount } from "./AddAmount";
import { getTransactions } from './utils/getTransactions';
import { Recap } from './Recap';
import { Stats } from './Stats';
import { getRecap } from './utils/getRecap';
import { budgetAndTransactions, mappedCosts } from './utils/budgetAndTransactions';
import { getMonth } from './utils/getMonth';
import { getSingleTransactions } from './utils/getSingleTransaction';

export const AppContext = createContext({});

export const SECTIONS = {
	CATEGORY: "category",
	ADD: "add",
	RECAP: "recap",
	STATS: "stats",
};

const theme = {
	text: '#164E63',
	mainColor: '#CFD8DC'
};

function App() {
	const [view, setView] = useState("category");
	const [allData, setAllData] = useState(undefined);
	const [rowData, setRowData] = useState(undefined);
	const [budget, setBudget] = useState();
	const [allTransactionOfMonth, setAllTransactionOfMonth] = useState();
	const [scope, setScope] = useState();

	const month = getMonth()

	const weekNumber = getWeek();
	useEffect(() => {
		getTransactions(setBudget)
		getSingleTransactions(month, setAllTransactionOfMonth)
		getRecap(setRowData);
	}, []);

	useEffect(() => {
		if(!rowData) return

		const variables = budgetAndTransactions(rowData)
		const {fixed, inc} = mappedCosts(rowData, month)
		setAllData({ variables, fixed, inc, allTransactionOfMonth })
	}, [rowData, allTransactionOfMonth, month])

	const openAdd = (id) => {
		setScope(id);
		setView(SECTIONS.ADD);
	};

	const openSection = (section) => {
		setView(section);
	};

	return (
		<AppContext.Provider value={{ openAdd, openSection, budget, allData }}>
			<ThemeProvider theme={theme}>
				<div className="app">

						{view === SECTIONS.ADD && budget !== undefined &&
							<AddAmount
								id={scope}
								weekNumber={weekNumber}
							/>
						}
						{view === SECTIONS.CATEGORY && budget !== undefined &&
							<Categories/>
						}
						{view === SECTIONS.RECAP &&
							<Recap/>
						}
						{view === SECTIONS.STATS &&
							<Stats/>
						}

				</div>
			</ThemeProvider>
		</AppContext.Provider>
	);
}

export default App;
